// banner组件接受以下属性与方法:
// 1. :height 改变banner的默认高度
// 2. :list	  banner的数据源,他们的数据格式是:
/* 
	[
		{
			title: 字符串:表示显示的默认标题内容,
			content: 字符串:显示副标题,
			imageUrl: 字符串:显示图片的网址,
			itemData: object:传递原始json数据,在banner点击item后接收事件@bannerItemClick(item)中可获取该数据
		},{
			title:   	同上,
			content: 	同上,
			imageUrl:	同上,
			itemData:	同上
		}
	]
 */
// 2.1 :dotColor	改变下方小白点的颜色,可以是#FFFFFF或者red/black等字符串
// 3. @bannerItemClick='事件名称'  该事件接受一个参数item,获取itemData传递进来的数据
export default {
	props: {
		// 指定banner高度
		height: {
			default: 200
		},
		// 指定banner数据源绑定
		// 
		list: {

		},
		dotColor: {
			default: 'white'
		}
	},
	data() {
		return {
			currentindex: 0,
		}
	},
	methods: {
		itemchange(e) {
			this.currentindex = e.detail.current
		},
		itemclick(e) {
			// console.log("log",e)
			this.$emit('bannerItemClick', e)
		}
	},
	computed: {}
}
